import React, { Component } from 'react';
import Website from './content/website.js';

import './App.css';
import 'w3-css/w3.css';
class App extends Component {
	
	
	
	constructor(props) {

    super(props);

	

    this.state = {

      windowWidth: 0,
      windowHeight: 0,	
	  breakeven:500,
	  screenType:type,
    };
	let type = this.updateDimensions.bind(this);
   
  }

  componentDidMount() {

    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions);
  }

  componentWillUnmount() {

    window.removeEventListener("resize", this.updateDimensions);
  }

  updateDimensions() {

    let windowWidth = typeof window !== "undefined" ? window.innerWidth : 0;
    let windowHeight = typeof window !== "undefined" ? window.innerHeight : 0;
	
	let type = 'L';
	if(windowWidth < 1300 ) type='M';
	else if(windowWidth < 600 ) type='S';
	
    this.setState({ screenType:type, windowWidth:windowWidth, windowHeight:windowHeight });

  }
  
  render() {
	  
    return (
      <div className="App" style={{maxWidth:'2000px',textAlign:'center',display: 'inlineBlock'}}>
		<Website device = { this.state.screenType } />
      </div>
    );
  }
  
  
 /*
  
	mainContent = function(){
		
		
	return ( 
		<div >
		{ this.state.windowWidth < 800 ? <HomeMobile/> : <Website device = { this.state.screenType } /> }
	</div>
	);
	
  }
  */
  
}

export default App;



