import React, { Component } from 'react';
import 'w3-css/w3.css';

import bullbear from './../images/bullbear.png';
import logo_bw from './../images/logo_bw.png';
import logo from './../images/logo_m.png';

import background from './../images/background.jpg';
import Header from './header.js';
import Features from './features.js';
import Footer from './footer.js';

import RollingSentences from './rollingsentences.js';
import Description from './description.js';
import Marketing from './marketing.js';
import nse from './../images/nse.png';
import zerodha from './../images/zerodha.svg';

import { text,locale,openZerodha,openNSE,terms } from './../util/i18n.js';

const styles = 
		{
			homeimg : {
				
				maxWidth:'80%',
				
				verticalAlign:'text-bottom',
			},

			
		};
	let nw = 0;

export default class HomeBody extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state ={pages:4,pageno:1};
	   
   }
   

	render(){
		
		return(
		
		<div >		
			<div className='w3-padding-small' style ={{ backgroundImage: "url(" + background + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box', backgroundAttachment: 'fixed', width:'100%', borderBottom:'1px solid #444'}} >	
				{/*<div class='w3-top' >
					<Header menuChanged = {this.menuChanged.bind(this)}/> 
				</div>
				*/}
				<div className='w3-text-black ' style={{ paddingTop:'30px', width:'100%'}} >						
						
					<p className="w3-opacity w3-left-align" style={{fontFamily:'Lucida', fontSize:'14px'}}>
						<i>"{text("quote")}..." <br/>
								{text("quote-desc")}</i>
					</p>			
					
				</div>
				<header className="w3-container  bgimg w3-center "  id="home">
					<div><img style={styles.homeimg} src={bullbear} alt="logo"  /></div>	
					<div className=" w3-center w3-text-black" >
					
						<h3  style={{ textShadow: '2px 2px 2px  #fff'}}>{text("sub-heading")}</h3>
						<h2  style={{ textShadow: '2px 2px 2px  #fff'}}>{text("main-heading")}</h2>											
						<button className="w3-button  w3-hover-light-gray w3-round w3-medium w3-margin-top w3-text-white" style={{backgroundColor:'black', color:'white'}} onClick={this.menuChanged.bind(this)}>{text("button-getstarted")}</button>
						
					</div>		
					
				</header>	
				<center>
				<div className='w3-margin-top '>
				<div className =' w3-padding w3-hide-small' style={{width:'410px' }}>	
					
						<RollingSentences device={this.props.device}/>	
						
						
					</div>
					</div>
				</center>
				<Features nmCards={1} />
				<Marketing nmCards ={1} device ={this.props.device}/>
				<div className=' w3-row  ' >
						<div className='w3-container w3-row w3-small w3-margin-left w3-left w3-padding' style={{maxWidth:'90%'}} >
								
							Trades on <a href='#' onClick={openNSE} >< img src={nse} style={{height:'12px'}} /></a>
							 &nbsp;Equity Margine Intraday Square @&nbsp;
							<a href='#' onClick={openZerodha} ><img src={zerodha} style={{height:'10px'}}/></a>.
						</div>
						<div className=' w3-small w3-margin-right w3-right ' style={{maxWidth:'90%'}} >
							<i>{text('caution')}</i> {text('tc')[0]}&nbsp;
							<a href ='#' onClick={terms} >{text('tc')[1]}</a>&nbsp;{text('tc')[2]}
						</div>	
					</div>
			</div>
		
		</div>
	
				
		);
	}
	
	
	menuChanged = function( menu ){
		
	  this.props.getStarted();
  }
  scrolled = function( event ){
	  console.log(new Date().getMilliseconds() - nw );
	if( nw - new Date().getMilliseconds()  > 500){
	   if(event.deltaY > 0 )
			this.next();
		else{
			this.prev();
		}
		nw = new Date().getMilliseconds();
	}
	 
  }
}




