import React, { Component } from 'react';
import 'w3-css/w3.css';
import './website.css'
import logo from './../images/logo.png';
import nse from './../images/nse.png';
import zerodha from './../images/zerodha.svg';


export default class Footer extends Component{
	
	constructor( props )
   {
       super(props);
   }
	
	render(){
		let islarge = this.props.device =='L';
		if( islarge )
			return(
				<div>
					
					<div className = '  w3-container w3-text-white w3-row w3-padding '	style={{height:'140px', backgroundColor:'#301A1A'}}>
					
						<div className='w3-col l4 w3-justify' style={{whiteSpace: 'nowrap'}}>
							<img src={logo} style={{height:'20px'}}/> <i>Prameyaka Technologies</i>							
						</div>
						<div className='w3-col l4' >&nbsp;</div> 
						<div className = ' w3-col l4' style={{whiteSpace: 'nowrap'}}>
							<i className="fa fa-envelope-o" aria-hidden="true"></i>
								&nbsp;<a  style={{textDecoration: "none" }} href = "mailto:contact@prameyaka.com" >contact@prameyaka.com</a>
									{/*<br/> 
								<i className="fa fa-phone" aria-hidden="true"></i>
									&nbsp;91-9538901718*/}
								
						</div>				
					</div>
				</div>
				
			);
		else {
			return(
				<div className = 'w3-container w3-black w3-text-white'	style={{height:'100px'}}>
				
					<div className='w3-padding' style={{whiteSpace: 'nowrap'}}>
						<img src={logo} style={{height:'20px'}}/> <i>Prameyaka Technologies</i>							
					</div>
					
					<div  style={{whiteSpace: 'nowrap'}}>		
						<i className="fa fa-envelope-o" aria-hidden="true"></i>
							&nbsp;<a  style={{textDecoration: "none" }} href = "mailto:contact@prameyaka.com" >contact@prameyaka.com</a>
								{/*<br/>
							<i className="fa fa-phone" aria-hidden="true"></i>
								&nbsp;91-9538901718*/}
							
					</div>				
				</div>
			);
		}
		
	}
}


