import React, { Component } from 'react';
import 'w3-css/w3.css';

import bullbear from './../images/bullbear.png';
import logo_bw from './../images/logo_bw.png';
import logo from './../images/logo_m.png';
import bgka from './../images/background.jpg';
import nse from './../images/nse.png';
import zerodha from './../images/zerodha.svg';
import bg3 from './../images/17.png';

import Header from './header.js';
import coming_soon from './../images/coming_soon.png';
import coming_soon_ka from './../images/coming_soon_ka.png';
import RollingSentences from './rollingsentences.js';
import Features from './features.js';
import Marketing from './marketing.js';
import Description from './description.js';
import "animate.css/animate.min.css";

import { text,locale,openZerodha,openNSE,terms } from './../util/i18n.js';
				 
const styles = 
		{
			homeimg : {
				height: '90px',
				verticalAlign:'text-bottom',
			},
			textsize :{
				fontSize:'18px'
			},
			theme:{
				//color:'#fafafa',
				color:'#fff',
				//color:'#211',
				//color:'#300a24',
				//color:'#1b0011',				
				
			},
			themeContrast:{
				color:'#fff',
				fontColor:'#000'
						
				
			},
			featureCard :{
			
				textAlign: 'center',
				
				fontFamily:'sans-serif',				
				fontSize:'14px',
				width:'30%', 
				float: 'left',
				lineHeight: 1.8,
			},
			
			vertiNavLeft: 'w3-button w3-block w3-left-align  w3-border-bottom w3-wide  w3-padding',
			vertiNavRight:'w3-button w3-block w3-right-align w3-border-bottom  w3-padding',
			
			vertiStyle :{
			letterSpacing:'1.5px',
			}
		};
	let nw = 0;

export default class HomeBody extends Component{
	
	constructor( props )
   {
       super(props);
	 
	   this.state ={pages:4,pageno:1,nw:1};
	  
	 
   }
   slide = function(){
	   
	   nw++;
	   if(nw > 4 ) nw=0;
	   this.setState({nw:nw});
	  // alert('hi');
   }
   
   componentDidMount() {
	  setInterval( this.slide.bind( this ), 5000 );
	  

   }

	render(){
		{/*
		let img = null;//locale() == 'ka' ? bgka : bgen;
		switch( nw ){
			case  0 :
			img = bgka;
			break;
			case  1 :
			img = bg1;
			break;
			case  2 :
			img = bg2;
			break;
			case  3 :
			img = bg3;
			break;
			case  4 :
			img = bg4;
			break;
			default :
			img = bgka;
			
			
		}*/}
		let img = bg3;
		//backgroundSize: 'cover', backgroundOrigin: 'border-box', backgroundAttachment: 'fixed', 
		return(
		<div   style ={{ backgroundImage: "url(" + img + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box', backgroundAttachment: 'fixed',}}>
			
			<div>
			
				<div className="w3-display-container " style={{ height:'340px'}} >	
				
					
					<div className=" w3-display-topleft w3-padding-32 ">						
								
							<p className="w3-left-align w3-padding-large w3-opacity" style={{fontFamily:'Georgia', fontSize:locale() == 'ka' ? '15px' : '17px' }}>
								<i>"{text("quote")}..." <br/>
								{text("quote-desc")}</i>
							</p>						
					</div>
					<div className ='w3-display-topright w3-padding-64' style={{  width:'28%' }} >
						<RollingSentences />	
					</div>
					<div className ='w3-display-left animate__animated animate__bounceInDown' style={{paddingLeft:'10px'}} >
						<img src={locale()=='en' ? coming_soon : coming_soon_ka} style={{height:'100px'}} className='w3-grayscale-max'/>
					</div>
										
						
					<div className="w3-display-bottommiddle   w3-center "  id="home">
						<div className=" w3-center w3-animate-opacity ">
							<img style={styles.homeimg} src={bullbear} alt="logo"  />
						</div>	
						<div className=" w3-center w3-text-black w3-animate-opacity" style={{color:styles.themeContrast.color,textShadow:'1px 1px 0 #ccc',letterSpacing: '3px'}}>					
							<div  style={{fontFamily: 'Helvetica, Arial, sans-serif', fontSize:'17px', textShadow: '1px 1px 1px  #fff', paddingTop:'10px'}}>
							{text("sub-heading")}
							</div>
							<div style={{fontFamily: 'Helvetica, Arial, sans-serif', fontSize:'34px', textShadow: '1px 1px 1px  #fff', paddingBottom:'14px'}}>
								{text("main-heading")}
								
								</div>											
							<button 
								className="w3-btn  w3-hover-brown w3-round w3-ripple   w3-shadow w3-border w3-border-brown " 
								style={{fontFamily: 'Helvetica, Arial, sans-serif', fontSize:'15px',  paddingTop:'10px',textTransform: 'uppercase'}}
								onClick={this.menuChanged.bind(this)}>
							
									{text("button-getstarted")}
								
								</button>
								
						</div>						
						{/*	<Description/>	} */}
	
					</div>				
				</div>	
				
				<Features nmCards ={1} />		
	
				<Marketing nmCards ={2} device={this.props.device}/>
				<div className=' w3-row  ' >
						<div className='w3-container w3-row w3-small w3-margin-left w3-left ' style={{maxWidth:'90%'}} >
								
							Trades on <a href='#' onClick={openNSE} >< img src={nse} style={{height:'12px'}} /></a>
							 &nbsp;Equity Margine Intraday Square @&nbsp;
							<a href='#' onClick={openZerodha} ><img src={zerodha} style={{height:'10px'}}/></a>.
						</div>
						<div className=' w3-small w3-margin-right w3-right ' style={{maxWidth:'90%'}} >
							<i>{text('caution')}</i> {text('tc')[0]}&nbsp;
							<a href ='#' onClick={terms} >{text('tc')[1]}</a>&nbsp;{text('tc')[2]}
						</div>	
					</div>
				
				
			</div>		
			
							
		</div>
		);
	}
	
	
	menuChanged = function( menu ){
	
	  this.props.getStarted();
	}
  
}




